import React from 'react';
import logoNoBg from '../../../logo_nobg.png'
import {Link} from "react-router-dom";

function LogoNoBg(props) {

    return (
        //   Route 
        <Link to={'/pi/sim'}>
          <img src={logoNoBg} className={'m-2'} style={{width : '20rem'}} alt=""/>
        </Link>
    )
}

export default LogoNoBg;