import React, {useEffect} from 'react';
import {Link} from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min";
import {OverlayTrigger, Popover} from "react-bootstrap";

function UserUI(props) {

    //    UI
    if (props.isLoggedIn)
        return (
            <div className={'d-flex align-items-center me-5'}>
                <OverlayTrigger
                    trigger="click"
                    key={'ui1'}
                    placement={'bottom'}
                    overlay={
                        <Popover>
                            <Popover.Header>
                                <div>
                                    <img src="" alt="" className={'rounded-5'}/>
                                    <p className={'text-center'}> or </p>
                                </div>
                            </Popover.Header>
                            <Popover.Body className={'text-center'}>
                                <Link to={'/pi/myPage'} className={'theme-link'}> </Link>
                                <hr/>
                                <Link to={'/pi/logout'} className={'theme-link'}></Link>
                            </Popover.Body>
                        </Popover>
                    }
                >
                    <a className={'theme-link'}>  </a>
                </OverlayTrigger>


                <OverlayTrigger
                    trigger="click"
                    key={'ui2'}
                    placement={'bottom'}
                    overlay={
                        <Popover>
                            <Popover.Body>
                                   
                            </Popover.Body>
                        </Popover>
                    }
                >
                    <a className={'text-black ms-3'}>
                        {props.newMessage
                            ? <i className="bi bi-bell"></i>
                            : <i className="bi bi-bell-fill position-relative">
                            <span
                                className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
                                    1 {/*  */}  </span>
                            </i>
                        }</a>
                </OverlayTrigger>

            </div>
        )
    //    UI
    return <div className={'d-flex justify-content-around align-items-center me-5'}>
        <h5>
            <Link to={'/userAuth/login'}className={'theme-link px-2 m-0'}></Link>
        </h5>
        <h5>
            <Link to={'/userAuth/signup'} className={'theme-link px-2 m-0'}></Link>
        </h5>
    </div>

}

export default UserUI;