import React from 'react';
import {Link, NavLink} from "react-router-dom";

function NavItem(props) {

    return (
        <li className={"nav-item"}>
            {/*    NavLink  */}
            <Link className={"theme-link-white mx-3"} aria-current="page"
                  to={props.to}>
                {props.name}
            </Link>
        </li>
    )
}

export default NavItem;