import React from 'react';
import logo from '../../../logo_pi.jpg'
import {Link} from "react-router-dom";

function Logo(props) {

    return (
        //   Route 
        <Link to={'/pi/sim'}>
          <img src={logo} className={'m-2'} style={{width : '10rem'}} alt=""/>
        </Link>
    )
}

export default Logo;